.Wrap {
  overflow-x: hidden;
}

.cbtn {
  display: inline-block;
  padding: 10px 20px;
  border: 1px solid #d41935;
  border-radius: 20px;
  color: #fff;
  background: #d41935;
  font-size: 13px;
  cursor: pointer;
  /* background: rgba(255, 255, 255, 0.7); */
}

.cbtn::after {
  content: "\e93e";
  display: inline-block;
  margin: 0 0 0 10px;
  font-family: xeicon;
  font-size: 11px;
  padding: 0 0 2px 0;
}

.csc {
  position: relative;
  padding: 100px 0;
}

.csc h2 {
  font-size: 27px;
  font-weight: 700;
  margin: 0 0 20px 0;
  text-align: center;
}

.csc p {
  font-size: 15px;
  margin: 0 0 40px 0;
  text-align: center;
}

.inner {
  position: relative;
  width: 1400px;
  margin: 0 auto;
}

.TopBanner {
  line-height: 50px;
  background: #f7f7f7;
  font-size: 13px;
}

.TopBanner .inner {
  display: flex;
  height: 50px;
  /* justify-content: center;
  align-items: center; */
  overflow: hidden;
}

.TopBanner .inner .banner {
  display: flex;
  flex-direction: column;
  align-items: center;
  top: 50%;
}

.TopBanner .inner .banner .txt {
  width: 800px;
  height: 50px;
  line-height: 50px;
  vertical-align: middle;
  text-align: center;
}

.TopBanner .service {
  display: flex;
  align-items: center;
  gap: 20px;
  margin: 0 0 0 auto;
  font-size: 13px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.8);
}

.TopBanner .service .lang_box {
  display: none;
}

