.Header {
  line-height: 100px;

}

.Header .inner {
  display: flex;
  justify-content: space-between;

}

.Header .Gnb,
.Header .top_service {
  width: 600px;
  z-index: 999;
}

.Header .top_search {
  margin: 0 0 0 auto;
}

.Header .Gnb>ul {
  display: flex;
  gap: 40px;
  text-align: center;
}

.Header .Gnb>ul>li {
  position: relative;
  font-size: 15px;
  font-weight: 500;
}

.Header .Gnb>ul>li>a {
  display: block;
}


.Header .Gnb>ul>li .sNav {
  display: none;
  position: absolute;
  top: 100px;
  left: 50%;
  transform: translateX(-50%);
  width: 140px;

  line-height: 2;
  border: 1px solid rgba(0, 0, 0, 0.2);
  background: #f7f7f7;
}

.Header .Gnb>ul>li .sNav>li {
  font-size: 15px;
  padding: 5px 0;
}

.Header .Gnb>ul>li .sNav>li~li {
  border-top: 1px solid rgba(212, 25, 53, 0.3);
}

.Header .mNav>li:hover .sNav {
  display: block;
  font-size: 15px;
  font-weight: 300;
}


.Header h1 {}

.Header h1 a {
  display: inline-block;
  width: 170px;
  height: 35px;
  background: url(../public/assets/images/logo.png) no-repeat center center/cover;
  font-size: 0;
  vertical-align: middle;
}

.Header .top_service {
  display: flex;
  gap: 20px;
  font-size: 23px;
}


.Header .top_service strong {
  cursor: pointer;
}

/* .Header .top_service strong:hover {
  color: #008850;
}

.Header .top_service strong.on {
  color: #008850;
} */
.Header .top_search {
  margin-right: 5px;
}

.Header .top_search .search_box {
  display: inline-block;
  width: 200px;
  text-align: center;
  vertical-align: middle;
}

.top_search form {
  display: flex;
  margin: auto 0 10px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.8);
}

.top_search .search_box input[type="text"] {
  font-size: 13px;
  border: none;
  outline: none;
  border-bottom: 2px solid var(--dc);
}

.top_search .search_box button {
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

.top_search .search_box button i {
  font-size: 21px;
}


.Header .top_service .login {
  position: relative;
}


.top_service .login .login_box {
  display: none;
  position: absolute;
  top: 100px;
  left: 50%;
  width: 120px;
  transform: translateX(-50%);
  line-height: 2;
  border: 1px solid rgba(0, 0, 0, 0.2);
  background: #f7f7f7;


}

.top_service .login_box>li {
  font-size: 13px;
  font-weight: 300;
  text-align: center;
  padding: 5px 0;
}

.top_service .login_box>li~li {
  border-top: 1px solid rgba(212, 25, 53, 0.3);
}


.Header .top_service .login.on .login_box {
  display: block;
}

.MainVisual {
  position: relative;
  color: var(--lc);
  height: 70vh;
}

.MainVisual figure {
  position: relative;
  height: 70vh;
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
}

/* 
.MainVisual figure.on {
  background-size: 100% auto;
  transition: 1s;
} */

.MainVisual .itm01 {
  background-image: url(../public/assets/images/main_visual01.jpg);
}

.MainVisual .itm02 {
  background-image: url(../public/assets/images/main_visual02_2.jpg);
}

.MainVisual .itm03 {
  background-image: url(../public/assets/images/main_visual03.jpg);
}


.MainVisual figure .inner {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.MainVisual .tit {
  font-size: 61px;
  font-weight: 700;
  margin: 0 0 40px 0;
  color: #ffffff;
  text-shadow: 0 0 2px #000;
  white-space: pre;
}

.MainVisual .des {
  font-size: 19px;
  /* font-weight: 300; */
  margin: 0 0 15px 0;
  color: #ffffff;
  text-shadow: 0 0 1px #000;
}

.MainVisual .slick-track,
.MainVisual .slick-slide {
  transition-duration: 5ms;
}

.MainVisual .slideTab {
  position: absolute;
  bottom: 100px;
  left: 50%;
  margin: 0 0 0 -700px;
  font-size: 13px;
  font-weight: 300;
  color: #f7f7f7;
}

.MainVisual .slideTab li {
  position: relative;
  display: inline-block;
  text-align: center;
  margin: 0 50px 0 0;
  /* display: inline-block; */
}

.MainVisual .slideTab li::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 0;
  display: inline-block;
  width: 0px;
  height: 1px;
  background: #f7f7f7;
  vertical-align: middle;
}

.MainVisual .slideTab li.on::after {
  width: 100px;
  transition: width 0.5s;
}

.MainVisual .slideNum {
  position: absolute;
  bottom: 100px;
  right: 50%;
  margin: 0 -700px 0 0;
  font-size: 23px;
  color: #f7f7f7;
}

.MainVisual .slideNum span {
  font-size: 13px;
  color: #ddd;
}

.MainVisual .slideArrows button:first-child {
  margin: 0 0 0 -800px;
}

.MainVisual .slideArrows button:last-child {
  margin: 0 0 0 800px;
}

.MainVisual .slideArrows button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 50px;
  color: #fff;
  cursor: pointer;
  background: transparent;
  border: none;
  outline: none;
  /* border-bottom: 1px solid #f9f9f9; */
  font-size: 0;
}

.MainVisual .slideArrows button:first-child::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 80px;
  height: 0;
  transform: translateY(-50%);
  border-top: 1px solid #fff;
}

.MainVisual .slideArrows button:last-child::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 80px;
  height: 0;
  transform: translateY(-50%);
  border-top: 1px solid #fff;
}


.MainVisual .slideArrows>button:last-child::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 20px;
  height: 0;
  transform-origin: right;
  transform: rotate(35deg);
  border-top: 1px solid #fff;
}

.MainVisual .slideArrows>button:first-child::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 20px;
  height: 0;
  transform-origin: left;
  transform: rotate(-35deg);
  border-top: 1px solid #fff;
}

.BestProduct {
  position: relative;
}

.BestProduct .tabtitle {
  padding: 0 0 50px 0;
}

.BestProduct .tabtitle>ul {
